import React, { useEffect, useState } from 'react';
import Skeleton from 'react-loading-skeleton';
import { useDispatch } from 'react-redux';
import { addCart } from '../redux/action';
import { useParams } from 'react-router';
import { NavLink } from 'react-router-dom';
import { Button, Col, Container, Image, Row } from 'react-bootstrap';

const Product = () => {

    const { id } = useParams();

    const [product, setProduct] = useState([]);
    const [loading, setLoading] = useState(false);

    const dispatch = useDispatch();
    const addProduct = (product) => {
        dispatch(addCart(product));
    }

    useEffect(() => {
        const getProduct = async () => {
            setLoading(true);
            const response = await fetch(`http://localhost:3333/episodes/${id}`);
            setProduct(await response.json());
            setLoading(false);
        }
        getProduct()
    }, []);


    const Loading = () => {
        return (
            <>
                <div className='col-md-6'>
                    <Skeleton height={400} />
                </div>
                <div className='col-md-6' style={{ lineHeight: 2 }}>
                    <Skeleton height={50} width={300} />
                    <Skeleton height={75} />
                    <Skeleton height={25} width={150} />
                    <Skeleton height={50} />
                    <Skeleton height={150} />
                    <Skeleton height={50} width={100} />
                    <Skeleton height={50} width={100} style={{ marginLeft: 6 }} />
                </div>
            </>
        )
    }

    const ShowProduct = () => {
        return (
            <>
                <Col className='md-6 d-flex justify-content-center pb-5'>
                    <Image src={product.image} alt={product.title} height='380px' width='380px' />
                </Col>
                <Col className='md-6'>
                    <h4 className='text-uppercase text-black-50'>
                        {product.category}
                    </h4>
                    <h1 className='display-5'>{product.title}</h1>
                    <p className='lead fw-folder'>
                        Rating {product.rating && product.rating.rate}
                        <i className='fa fa-star'></i>
                    </p>
                    <h3 className='display-6 fw-bold my-4'>$ {product.price}</h3>
                    <p className='lead'> {product.description}</p>
                    <Button variant='transparent' className='btn btn-outline-dark px-4 py-2' onClick={() => addProduct(product)}>Add to Cart</Button>
                    <NavLink to='/cart' className='btn btn-dark ms-2 px-3 py-2'>Go to Cart</NavLink>
                </Col>
            </>
        )
    }

    return (
        <Container className='py-5'>
            <Row className='gap-7'>
                {
                    loading ?
                        <Loading />
                        :
                        <ShowProduct />
                }
            </Row>
        </Container>
    )
}

export default Product